<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-col :span="4"
      ><el-button type="primary" @click="Sent"></el-button></el-col
    >

    <el-table
      :data="tableData"
      style="width: 100%"
      row-key="id"
      border
      lazy
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="date" label="" >
      </el-table-column>
      <el-table-column prop="name" label="" >
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <div>
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="UpdateFun(scope.$index)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="open(scope.$index)"
              ></el-button
            >
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button size="mini" type="warning" icon="el-icon-s-tools"
                ></el-button
              >
            </el-tooltip>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :model="FromDia"
      title=""
      :visible.sync="dialogVisible"
      width="50%"
    >
      <el-form :model="FromDia" ref="addform" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="FromDia.date"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="FromDia.name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="AddFrom"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      :model="FromDia"
      title=""
      :visible.sync="update"
      width="50%"
    >
      <el-form :model="updated" ref="addform" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="updated.date"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="updated.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="update = false"> </el-button>
        <el-button type="primary" @click="Updates(updated.i)"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      updated: {
        i:'',
        date:'',
        name:''
      },
      update: false,
      FromDia: {
        date: "",
        name: "",
      },
      dialogVisible: false,
      tableData: [
        {
          date: "",
          name: "",
        },
        {
          date: "",
          name: "",
        },
        {
          date: "",
          name: "",
        },
        {
          date: "",
          name: "",
        },
      ],
    };
  },
  methods: {
    Sent() {
      this.dialogVisible = true;
    },
    AddFrom() {
      if (this.FromDia.date != "") {
        const newarr = {
          date: this.FromDia.date,
          name: this.FromDia.name,
        };
        this.tableData.push(newarr);
        this.dialogVisible = false;
        this.$message.success("");
        this.FromDia.date = "";
        this.FromDia.name = "";
      } else {
        this.$message.error("");
      }
    },
    open(i) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.tableData.splice(i, 1);
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    UpdateFun(i){
        this.updated.i=i
        this.updated=this.tableData[i]
        this.update=true
    },
    Updates(i){
        this.tableData[i]=this.updated
        this.update=false
        this.$message.success("");
        
    }
  },
};
</script>

<style scoped>
.el-table {
  margin-top: 70px;
}
</style>