//  echarts  echarts 
import * as echarts from 'echarts/core';
//  Chart
import { BarChart, PieChart,LinesChart ,GaugeChart,EffectScatterChart  } from 'echarts/charts';
//  Component
import {
    TitleComponent,
    TooltipComponent,
    GridComponent,
    DatasetComponent,
    TransformComponent,
    LegendComponent,
    GeoComponent
} from 'echarts/components';
// 
import { LabelLayout, UniversalTransition } from 'echarts/features';
//  Canvas  CanvasRenderer  SVGRenderer 
import { CanvasRenderer } from 'echarts/renderers';

// 
echarts.use([
    GeoComponent,
    LegendComponent,
    TitleComponent,
    TooltipComponent,
    GridComponent,
    DatasetComponent,
    TransformComponent,
    BarChart,
    LabelLayout,
    UniversalTransition,
    CanvasRenderer,
    PieChart,
    LinesChart,
    GaugeChart,
    EffectScatterChart 

]);

export default echarts